'use client';

import React from 'react';
import { useMockAuth } from '../../hooks/useMockAuth';
import LoginForm from '../../components/LoginForm';
import LoginPanel from '../../components/LoginPanel';
import WelcomePanel from '../../components/WelcomePanel';

export default function LoginPage() {
  const { login, isLoading, error, clearError } = useMockAuth();

  const handleLogin = async (formData: any) => {
    clearError(); // Limpiar errores previos
    await login(formData);
  };

  return (
    <div className="h-screen w-full bg-[url('/images/loginBg.png')] bg-center bg-no-repeat bg-cover flex items-center justify-center p-4">
      {/* Contenedor principal con bordes redondeados */}
      <div className="w-full max-w-6xl h-[600px] bg-white shadow-2xl overflow-hidden flex">
        {/* Panel izquierdo - Bienvenida */}
        <div className="w-1/2">
          <WelcomePanel />
        </div>
        
        {/* Panel derecho - Formulario de login */}
        <div className="w-1/2">
          <LoginPanel>
            <div className="max-w-md mx-auto">
              
              {/* Mostrar error si existe */}
              {error && (
                <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-6">
                  {error}
                </div>
              )}
              
              <LoginForm onSubmit={handleLogin} isLoading={isLoading} />
            </div>
          </LoginPanel>
        </div>
      </div>
    </div>
  );
}
