import React from 'react';

interface LoginPanelProps {
  children: React.ReactNode;
}

const LoginPanel: React.FC<LoginPanelProps> = ({ children }) => {
  return (
    <div className="bg-white p-8 rounded-r-3xl w-full h-full flex flex-col justify-center">
      {children}
    </div>
  );
};

export default LoginPanel;
